export enum FieldType {
    Timestamp = "timestamp", // string
    Number = "number", // number
    TrueFalse = "trueFalse", // boolean
    SelectValue = "selectValue", // string
    Text = "text", // string
    // File = 'file' // string (base64)
}

export type FieldValueType = string | number | boolean | null;

type FieldBase = {
    isRequired: boolean;
    key: string;
    label: string;
    index: number;
}

type AnyField = FieldBase & {
    fieldType: Exclude<FieldType, FieldType.SelectValue | FieldType.TrueFalse>
}

type TrueFalseField = FieldBase & {
    fieldType: FieldType.TrueFalse;
    isRequired: true;
}

export type SelectableOption = { value: string, label: string };

type SelectValueField = FieldBase & {
    fieldType: FieldType.SelectValue;
    options: SelectableOption[];
};

export type Field = AnyField | SelectValueField | TrueFalseField;

export type MetaModel = {
    fields: Record<string, Field>;
    version: string;
}

export type Model = Record<string, FieldValueType>;

export const isTrueFalseField = (field: Field): field is TrueFalseField => field.fieldType === FieldType.TrueFalse;
export const isSelectValueField = (field: Field): field is SelectValueField => field.fieldType === FieldType.SelectValue;