import { ChangeEventHandler, FC, useCallback } from "react";
import parseISO from "date-fns/parseISO";
import format from "date-fns/format";

type TimestampProps = {
  fieldKey: string;
  label: string;
  isRequired: boolean;
  value: string | null;
  onChange: (value: string | null) => void;
}

const toPickerStringFormat = "yyyy-MM-dd hh:mm";

export const Timestamp: FC<TimestampProps> = ({
	fieldKey,
	label,
	value,
	onChange,
}) => {
	const handleTimeChange = useCallback<ChangeEventHandler<HTMLInputElement>>(e => {
		onChange(e.target.value || null);
	}, [onChange]);

	const displayValue = value ? format(parseISO(value), toPickerStringFormat) : "";

	return (
		<>
			<label htmlFor={fieldKey}>{label}</label>
			<input
				style={{ fontSize: "16px" }}
				id={fieldKey}
				type="datetime-local"
				value={displayValue}
				onChange={handleTimeChange} />
		</>
	);
};