const getDefaultValues = (metaModel: MetaModel): Model => 
	Object.values(metaModel.fields).reduce((model, field) => {
		switch (field.fieldType) {
		case FieldType.Timestamp: {
			return { ...model, [field.key]: new Date().toISOString() };
		}

		case FieldType.TrueFalse: {
			return { ...model, [field.key]: false };
		}

		default: {
			return {
				...model, [field.key]: "",
			};
		}
		}
	}, {});

type ValueChangeHandler = (value: FieldValueType) => void;

export const Logger: FC = () => {
	const sortedFields = useMemo(() => Object.values(metaModel.fields).sort((a, b) => a.index - b.index), [metaModel]);
	const [values, setValues] = useState(getDefaultValues(metaModel));
	const handlers = useMemo<Record<string, ValueChangeHandler>>(() => {
		return sortedFields.reduce((handlerCollection, field) => ({
			...handlerCollection,
			[field.key]: (value: FieldValueType) => setValues(prev => ({ ...prev, [field.key]: value })),
		}), {});
	}, []);
	const { appendRecord } = useSheetActions();
	const handleSubmission = useCallback(
		async () => {
			await appendRecord(
				values.timestamp as string,
				values.action as string,
				`${values.comment || ""}`,
			);
		},
		[values],
	);

	return (
		<div>
			<form onSubmit={handleSubmission}>
				<div style={{ display: "flex", flexDirection: "column" }}>
					{sortedFields.map(field => {
						const { fieldType, label, key, isRequired } = field;

						if (fieldType === FieldType.Timestamp) {
							return (
								<Timestamp
									key={key}
									fieldKey={key}
									label={label}
									onChange={handlers[key]}
									isRequired={isRequired}
									value={values[key] as string} />
							);
						}

						if (isTrueFalseField(field)) {
							return (
								<TrueFalseField
									key={key}
									fieldKey={key}
									label={label}
									onChange={handlers[key]}
									value={(values[key] ?? false) as boolean} />
							);
						}

						if (isSelectValueField(field)) {
							return (
								<SelectValue
									key={key}
									fieldKey={key}
									label={label}
									value={(values[key] ?? null) as string}
									options={field.options}
									isRequired={isRequired}
									onChange={handlers[key]}
								/>
							);
						}

						if (field.fieldType === FieldType.Number) {
							return (
								<NumberField
									key={key}
									fieldKey={key}
									label={label}
									value={(values[key] ?? null) as number}
									onChange={handlers[key]}
									isRequired={isRequired} />
							);
						}

						if (field.fieldType === FieldType.Text) {
							return (
								<TextField
									key={key}
									fieldKey={key}
									label={label}
									value={(values[key] ?? null) as string}
									onChange={handlers[key]} />
							);
						}

						return null;
					})}
					<button type="submit">Hit me baby!</button>
				</div>
			</form>
		</div>
	);
};
