import { ChangeEventHandler, FC, useCallback, useEffect } from "react";
import { SelectableOption } from "../types";

type SelectValueProps = {
  fieldKey: string;
  label: string;
  isRequired: boolean;
  value: SelectableOption["value"] | null;
  options: SelectableOption[];
  onChange: (value: SelectableOption["value"] | null) => void;
}

export const SelectValue: FC<SelectValueProps> = ({
	fieldKey,
	label,
	isRequired,
	value,
	options,
	onChange,
}) => {
	useEffect(() => {
		if (value) { return; }
		if (!options || options.length === 0) { return; }
		if (isRequired) {
			onChange(options[0]?.value ?? null);
		}
	}, [isRequired, value, options, onChange]);

	const handleSelect = useCallback<ChangeEventHandler<HTMLSelectElement>>(
		e => onChange(e.target.value),
		[onChange],
	);

	return (
		<>
			<label htmlFor={fieldKey}>{label}</label>
			<select id={fieldKey} value={value ?? ""} onChange={handleSelect}>
				{!isRequired && <option value=""></option>}
				{options.map(option => (
					<option key={option.value} value={option.value}>{option.label}</option>
				))}
			</select>
		</>
	);
};