import { useState, useEffect } from "react";
import { gapi, loadAuth2 } from "gapi-script";

const CLIENT_ID = "974472976860-issciim2t14g1dipe17ldjb28347qu18.apps.googleusercontent.com";
const SCOPES = "https://www.googleapis.com/auth/spreadsheets";

export const Authentication = () => {
	const [user, setUser] = useState<{ name: string; avatar: string }|undefined>();
	const [auth2, setAuth2] = useState<gapi.auth2.GoogleAuthBase>();

	useEffect(() => {
		loadAuth2(gapi, CLIENT_ID, SCOPES).then(auth2 => {
			if (auth2.isSignedIn.get()) {
				updateUser(auth2.currentUser.get());
			}
			setAuth2(auth2);
		});
	}, []);
  
	useEffect(() => {
		if (!user) {
			loadAuth2(gapi, CLIENT_ID, SCOPES).then(auth2 => {
				if (auth2.isSignedIn.get()) {
					updateUser(auth2.currentUser.get());
				}
				setAuth2(auth2);
			});
		}
	}, [user]);

	const updateUser = (currentUser: gapi.auth2.GoogleUser) => {
		const name = currentUser.getBasicProfile().getName();
		const avatar = currentUser.getBasicProfile().getImageUrl();
		setUser({
			name,
			avatar,
		});
	};

	const handleSignIn = () => {
		auth2?.signIn().then(updateUser);
	};

	const signOut = () => {
		auth2?.signOut().then(() => {
			setUser(undefined);
		});
	};

	if(user) {
		return (
			<div className="container">
				<img src={user.avatar} height={48} width={48} />
				<div id="" className="btn logout" onClick={signOut}>
          Logout
				</div>
			</div>
		);
	}

	return (
		<div className="container">
			<button type="button" onClick={handleSignIn}>
        Login
			</button>
		</div>
	);
};