import { gapi } from "gapi-script";
import { useCallback, useEffect, useState } from "react";

const API_KEY = "AIzaSyDcbkktXHZ_kVHZvsvR9qJh-r57OzvMjiI";
const DISCOVERY_DOC = "https://sheets.googleapis.com/$discovery/rest?version=v4";

export const useSheetActions = () => {
	const [ready, setReady] = useState(false);
	useEffect(() => {
		gapi.load("client", async () => {
			await gapi.client.init({
				apiKey: API_KEY,
				discoveryDocs: [DISCOVERY_DOC],
			});
			setReady(true);
		});
	}, []);

	const appendRecord = useCallback(async (timestamp: string, action: string, comment: string) => {
		if (!ready) { return; }
		await (<any>gapi.client).sheets.spreadsheets.values.append({
			valueInputOption: "RAW",
			spreadsheetId: "1zvLL-4WRhVJRtgw59b14-1dmRxgJgoH4F_oKSLw1d4c",
			range: "Sheet2!A3",
			majorDimension: "ROWS",
			values: [
				[timestamp, action, comment],
			],
		});
	}, [ready]);

	return { appendRecord };
};
